import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dark: true
  },
  mutations: {
    dark (state, dark) { state.dark = dark }
  },
  getters: {
    dark: state => state.dark
  },
  plugins: [
    createPersistedState({
      paths: [
        'dark'
      ]
    })
  ]
})
