    return {
      menuItems: [
        { ref: 'top', name: 'TOP' },
        { ref: 'works', name: 'WORKS' },
        { ref: 'contact', name: 'CONTACT' },
      ],
      profile: {
        image: 'https://pbs.twimg.com/profile_images/1351197894381072387/Ecgwh9SR_400x400.jpg',
        name: 'Tsukusu Endo / 遠藤 尽',
        message: `
          <p>2000 年生まれ、学生です。
          <p>フリーランスでエンジニアをやっています。
          フルスタックな開発でプロダクトリリースをしています。
