          <p>Python や JavaScript あたりが無難に好きです。Django や Vue, Firebase, AWS などを日々扱います。
        `,
        socials: [
          {
            name: 'GitHub',
            icon: 'fab fa-github-square',
            url: 'https://github.com/tsuxendo'
          },
          {
            name: 'Twitter',
            icon: 'fab fa-twitter-square',
            url: 'https://twitter.com/tsuxendo'
          },
          {
            name: 'FaceBook',
            icon: 'fab fa-facebook-square',
            url: 'https://facebook.com/tsuxendo'
          },
          {
            name: 'Mail',
            icon: 'fas fa-envelope-square',
            url: 'mailto:tsux@tsux.me'
          }
        ]
      },
      works: [
        {
          name: 'joicul',
          url: 'https://sites.google.com/tsux.me/joicul/',
          image: 'https://joicul.web.app/img/icons/android-chrome-192x192.png',
          title: 'Joicul',
          subtitle: 'エンジニアコミュニティサービス',
          message: 'エンジニアのコミュニティであり、サポートであり、開発チームである。そんな存在を目指して立ち上げた事業です。メンバー用の決済アプリも作成しました。 Joicul 発足以降は個人だけでなく、 Joicul Team という Joicul 事業内の開発チームとしてもサービス開発を行います。 ( 2020/11 ~ )'
        },
        {
          name: 'comame',
          url: 'https://commercial-and-me.com/',
          image: 'https://commercial-and-me.com/img/ogp.png',
          title: 'comame ホームページ',
          subtitle: '広告をもっと面白くする',
          message: '「広告をもっと面白くする」をモットーにした学生の事業、comame で企画開発を担当 ( 2020/4 ~ ) し、ホームページの制作を行いました ( 2021/1 )'
        },
        {
          name: 'coursevalu',
          url: 'https://app.coursevalu.com/',
          image: 'https://app.coursevalu.com/img/icons/android-chrome-192x192.png',
          title: 'courseVALU',
          subtitle: '大学用匿名コミュニティサービス（courseVALU合同会社）',
          message: 'coursevalu 合同会社様で業務委託ながら CTO として仕事をさせていただき、サービス開発を行っています。 ( 2020/05 ~ )'
        },
        {
          name: 'otobanana',
          url: 'https://otobanana.com/',
          image: 'https://otobanana.com/img/logo.png',
          title: 'OTOBANANA',
          subtitle: '成人向け音声配信サービス（otonomics株式会社）',
          message: 'otonomics 株式会社様で業務委託ながら CTO として仕事をさせていただき、サービス開発を行っています。 ( 2020/04 ~ )'
        },
        {
          name: 'syumidia',
          url: 'https://syumidia.com/',
          image: 'https://syumidia-assets.s3.amazonaws.com/static/img/logo.png',
          title: '趣midia',
          subtitle: '趣味探しを応援するメディア',
          message: '個人からのご依頼で、趣味探しを応援するメディアサイトのデザイン・開発を行いました。 ( 2020/05 )'
        },
        {
          name: 'kyodaibuken',
          url: 'https://kyobuken.com/',
          image: 'https://kyobuken.com/static/img/favicon.ico',
          title: '京大舞研',
          subtitle: '京都大学舞踏研究会 ホームページ',
          message: '京都大学舞踏研究会からのご依頼で、ホームページのデザイン・開発を行いました。 ( 2020/04 )'
        }
      ],
      form: {
        name: '',
        email: '',
        message: ''
      },
      rules: {
        name: [v => !!v || 'お名前を入力してください'],
        email: [v => regExpEmail.test(v) || 'メールアドレスが正しくありません'],
        message: [v => !!v || 'ご用件を入力してください'],
      },
    }
  },
  methods: {
    scrollTo (ref) {
      const target = this.$refs[ref].$el
      window.scrollTo({
        top: target.offsetTop,
        behavior: 'smooth'
      })
    },
    async submitForm () {
      if (!this.$refs.form.validate()) return
      const confirm = window.confirm('お問い合わせを送信しますか？')
      if (!confirm) return
      try {
        const data = new FormData()
        for (const key in this.form) { data.append(key, this.form[key]) }
        await this.sendToFormspree(data)
        alert('お問い合わせを送信しました')
        this.$refs.form.reset()
      } catch (e) {
        alert(`
          送信に失敗しました。入力が間違っていないか確認してください。\n
          それでも送信に失敗する場合は、時間を置いてもう一度送信していただくか、 SNS よりご連絡ください。
        `)
      }
    },
    sendToFormspree (data) {
      return new Promise (function (resolve, reject) {
        const url = 'https://formspree.io/f/xleoppvg'
        const xhr = new XMLHttpRequest()
        xhr.open('POST', url)
        xhr.setRequestHeader('Accept', 'application/json')
        xhr.onreadystatechange = () => {
          if (xhr.readyState !== XMLHttpRequest.DONE) return
          if (xhr.status === 200) {
            resolve(xhr.response)
          } else {
            reject(new Error(xhr.status))
          }
        }
        xhr.send(data)
      })
    }
  },
  computed: {
    xs () { return this.$vuetify.breakpoint.name === 'xs' }
  }
}
</script>
